import React from 'react';
import Markdown from 'react-markdown';

class StaticPage extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            md: ""
        };
    }
    componentDidMount() {
        fetch(this.props.params.md)
        .then(res => res.text())
        .then((result) => {
            this.setState({
                md: result
            });
        }, (error) => {
            console.error(error);
        });
    }
    render() {
        return <Markdown source={this.state.md} />
    }
}

export default StaticPage;